/* Footer Styles */
.site-footer {
  background: linear-gradient(to right, #1a2a3a, #2c3e50);
  color: #fff;
  padding: 60px 0 20px;
  font-family: 'Inter', sans-serif;
}

.footer-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-column {
  flex: 1;
  min-width: 250px;
  margin-bottom: 30px;
  padding: 0 15px;
  box-sizing: border-box;
}

.footer-logo {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.footer-logo img {
  height: 40px;
  width: auto;
  margin-right: 10px;
  border-radius: 5px;
  background-color: white;
  padding: 5px;
}

.footer-logo h3 {
  margin: 0;
  font-size: 1.3rem;
  font-weight: 700;
  color: #f0a500;
  font-style: italic;
}

.footer-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: #ccc;
  margin-bottom: 20px;
}

.social-icons {
  display: flex;
  gap: 15px;
}

.social-icons a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
  transition: all 0.3s ease;
  text-decoration: none;
  overflow: visible;
}

.social-icons a i {
  font-size: 20px;
}

.social-icons a:hover {
  /* background-color: #f0a500;  */
  transform: translateY(-3px);
}

.footer-column h4 {
  color: #f0a500;
  font-size: 1.2rem;
  margin-bottom: 20px;
  position: relative;
  padding-bottom: 10px;
}

.footer-column span{
  color:#eee;
  font-family: 'Inter', sans-serif;
  text-align: center;
  width: auto;
  height:auto;
}

.footer-column h4::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50px;
  height: 2px;
  background-color: #f0a500;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 12px;
}

.footer-links a {
  color: #ccc;
  text-decoration: none;
  transition: color 0.3s ease;
  display: inline-block;
  position: relative;
}

.footer-links a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 1px;
  bottom: -2px;
  left: 0;
  background-color: #f0a500;
  transition: width 0.3s ease;
}

.footer-links a:hover {
  color: #f0a500;
}

.footer-links a:hover::after {
  width: 100%;
}

.contact-info {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contact-info li {
  display: flex;
}

/* Responsive Styles */
@media (max-width: 900px) {
  .footer-top {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .footer-column {
    min-width: 220px;
    max-width: 50%;
    flex-basis: 45%;
    margin-bottom: 30px;
    padding: 0 10px;
  }
}

@media (max-width: 600px) {
  .site-footer {
    padding: 40px 0 10px;
  }
  .footer-top {
    flex-direction: column;
    padding: 0 10px;
  }
  .footer-column {
    min-width: 0;
    max-width: 100%;
    flex: 1 1 100%;
    margin-bottom: 25px;
    padding: 0 5px;
  }
  .footer-logo {
    flex-direction: row;
    justify-content: flex-start;
  }
  .footer-logo img {
    height: 32px;
    padding: 3px;
  }
  .footer-column h4 {
    font-size: 1.1rem;
    padding-bottom: 6px;
  }
  .footer-column h4::after {
    width: 35px;
    height: 2px;
  }
  .social-icons a {
    width: 36px;
    height: 36px;
    font-size: 18px;
  }
  .footer-description {
    font-size: 0.92rem;
  }
  .footer-links li, .contact-info li {
    margin-bottom: 10px;
  }
  .footer-links a {
    font-size: 1rem;
  }
}

/* Optional: add a subtle divider for mobile */
@media (max-width: 600px) {
  .footer-column:not(:last-child) {
    border-bottom: 1px solid rgba(255,255,255,0.07);
    padding-bottom: 16px;
    margin-bottom: 16px;
  }
}